<template>
  <main>
    <div class="main">
      <div class="wrap">
        <span class="ribbon6"></span>
      </div>
      <h2></h2>
      <div class="line"></div>
      <h3></h3>
      <div id="main"></div>
      <h3></h3>
      <ul>
        <li><p> <span class="bold"></span> VC JAVA SQL WEB </p></li>
        <li><p> <span class="bold">HTML(5)</span>  <span class="bold">CSS(3)</span>  <span class="bold"></span> UI</p></li>
        <li><p> <span class="bold">JavaScript</span> <span class="bold">this</span>  jQuery  API  <span class="bold">ES6ES7</span> </p></li>
        <li><p> <span class="bold"> Vue </span> <span class="bold"></span> </p></li>
        <li><p> <span class="bold"></span>  </p></li>
        <li><p> <span class="bold">HTTP</span>  <span class="bold"></span> </p></li>
        <li><p> <span class="bold">Git</span> GitHub</p></li>
      </ul>
    </div>
  </main>
</template>

<script>
export default {
  name: 'Skill',
  mounted () {
    var myChart = this.$echarts.init(document.getElementById('main'))
    var option = {
      tooltip: {},
      radar: {
        shape: 'circle',
        name: {
          textStyle: {
            color: '#fff',
            backgroundColor: '#999',
            borderRadius: 3,
            padding: [3, 5]
          }
        },
        indicator: [
          {name: 'JavaScript', max: 100},
          {name: 'JQuery', max: 100},
          // {name: 'React', max: 100},
          {name: '', max: 100},
          {name: 'Vue', max: 100},
          {name: 'HTTP', max: 100},
          {name: '', max: 100},
          {name: 'HTML/CSS', max: 100}
        ]
      },
      series: [
        {
          name: ' vs Budget vs spending',
          type: 'radar',
          data: [
            {
              value: [85, 75, 80, 80, 85, 85, 90],
              name: ''
            }
          ]
        }
      ]
    }
    myChart.setOption(option)
  }
}
</script>

<style scoped>
.main > h2 {
  user-select: none;
}
.main > .line {
  width: 120px;
}
#main {
  width: 400px;
  height: 260px;
  max-width: 90vw;
  align-self: center;
}
ul {
  margin: 0;
}
span.bold {
  font-weight: bold;
}
</style>



